import React from 'react';
import { NavLink } from 'react-router-dom';
import web from "../src/images/img.jpg";
const Comm = (props) => {
    return (
        <>
            <section id="header" className="d-flex align-items-center">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-10 mx-auto">
                        <div className="row">
                            <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex flex-column justify-content-center">
                              <h1>{props.name} <strong className="brand-name">AsitTechnical</strong></h1>
                              <h2 className="my-3">We are the team of talented devloper making website</h2>

                              <div className="mt-3">
                                <NavLink className="btn-get-started" to={props.visit}>{props.btnName}</NavLink>
                              </div>
                            </div>

                            <div className="col-lg-6 order-1 order-lg-2 header-img">
                               <img src={props.imgsrc} className="img-fluid animated" alt="sss"/>
                            </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}

export default Comm;